import {Component, EventEmitter, OnInit, Output} from '@angular/core';
import {DataService} from '../data.service';
import {Card} from '../card';

@Component({
  selector: 'app-list',
  templateUrl: './list.component.html',
  styleUrls: ['./list.component.css'],
  providers: [DataService]
})
export class ListComponent implements OnInit {
  posts: Card[] = [];
  formVisibility = false;
  textToAdd: string;
  detailedPost: Card;
  showPost :boolean = false;
  detailedIndex: number;
  title: string = 'Main';

  constructor(private dataService: DataService){}
  showDetailedPost(cardToShow){
    this.detailedPost = this.posts[cardToShow];
    this.showPost = !this.showPost;
    this.detailedIndex = cardToShow;
  }
  saveCard(){
    this.dataService.saveCard();
  }
  closeCard(close){
    this.showPost = close;
  }
  toggleForm() {
    this.formVisibility = !this.formVisibility;
  }
  addCard(text: string) {
    this.dataService.addCard(text);
    this.textToAdd = '';
  }
  deleteCard(index){
    this.dataService.deleteCard(index);
  }

  @Output() onShow = new EventEmitter<Card>();
  showDetailed(index){
    this.onShow.emit(this.posts[index]);
  }
  ngOnInit() {
    this.posts = this.dataService.getCards();
  }

}



