import { Injectable } from '@angular/core';
import {Card} from './card';

@Injectable()
export class DataService {
  private data: Card[];

  addCard(text: string) {
    (text !== '' && text !== undefined) ? this.data.push(new Card(text)) : alert('Enter your text please');
    localStorage.setItem("cards", JSON.stringify(this.data));
  }
  getCards() {
    localStorage.getItem("cards") !== null ? this.data = JSON.parse(localStorage.getItem("cards")) : this.data = [];
    return this.data;
  }
  deleteCard(index: number) {
    this.data.splice(index, 1);
    localStorage.setItem("cards", JSON.stringify(this.data));
  }

  saveCard() {
    localStorage.setItem("cards", JSON.stringify(this.data));
  }
}



